.calorie-counter {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #101010;
    border-radius: 20px;
    padding: 20px;
    width: 90%;
    max-width: 600px;
    margin: -15px auto 25px;
    color: white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    align-self: flex-start;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Ü–µ–Ω—Ç—Ä–∞–ª—å–Ω–æ–≥–æ –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ –∏ –¥–≤—É—Ö –±–æ–∫–æ–≤—ã—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
.main-progress-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

/* –û—Å–Ω–æ–≤–Ω–æ–π –∫—Ä—É–≥ */
.main-circle {
    position: relative;
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* –¢–µ–∫—Å—Ç –≤ —Ü–µ–Ω—Ç—Ä–µ –∫—Ä—É–≥–∞ */
.main-text {
    position: absolute;
    text-align: center;
}

.main-text h2 {
    font-size: 24px;
    font-weight: bold;
}

.main-text p {
    font-size: 14px;
    color: #bbb;
}

/* Supplied & Burned (–¢–µ–ø–µ—Ä—å –Ω–∞ –æ–¥–Ω–æ–π –ª–∏–Ω–∏–∏ —Å –∫—Ä—É–≥–æ–º) */
.calories-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    min-width: 80px;
}

.supplied h3, .burned h3 {
    font-size: 18px;
    font-weight: bold;
}

.supplied span, .burned span {
    font-size: 18px;
    color: #4ade80;
}

.burned span {
    color: #f87171;
}

/* –ë–ª–æ–∫ —Å –º–∞–∫—Ä–æ–Ω—É—Ç—Ä–∏–µ–Ω—Ç–∞–º–∏ (–¢–µ–ø–µ—Ä—å –æ–Ω–∏ –∏–¥—É—Ç –≤ –æ–¥–∏–Ω —Ä—è–¥) */
.macronutrients {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 15px;
}

.macro {
    text-align: center;
}

.macro p {
    font-size: 12px;
    color: #bbb;
    margin-top: 5px;
}